li {
  list-style: none;
}

a {
  text-decoration: none;
}

.article-section {
  background-color: #fff5f6;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.article-section__site--city,
.article-section__site--paragraph {
  width: 179px;
}
.article-section__site--city {
  margin-bottom: 10px;
  margin-top: 0;
}

.article-section__site--paragraph {
  margin-bottom: 0;
  margin-top: 0;
}

.article-section--border {
  border-bottom: 1px solid #e1e1e1;
  width: auto;
  margin: 25px 20px 25px 20px;
}
.article-section__site--days {
  font-weight: 500;
  font-size: 12px;
  line-height: 186%;
  color: #000000;
  margin-top: 0;
  margin-bottom: 0;
}
.article-section__site--times {
  font-weight: 500;
  font-size: 12px;
  line-height: 186%;
  color: #ffa5ba;
  margin-top: 0;
  margin-bottom: 10px;

  &__second {
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: #ffa5ba;
    margin-left: 20px;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.article-section__link--tel,
.article-section__link--mail {
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  color: #907e82;
}

.article-section__link--mail {
  margin-bottom: 43px;
}

.article-section__navigation {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}

.article-section__site {
  display: flex;
  flex-direction: column;
  width: 220px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  margin: 0 0 20px 0;
  background-color: #ffffff;

  @media screen and (min-width: 768px) {
    margin-right: 20px;
  }
}

.article-section__site:last-child {
  margin-bottom: 54px;
  @media screen and (min-width: 768px) {
    margin-bottom: auto;
    margin-right: 0;
  }
}

.article-section__site--locations {
  width: 80px;
  color: #ffffff;
  background-color: #f0d1a5;
  border-radius: 4px;
  margin: 43px 120px 25px 20px;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.04em;
  line-height: 186%;
}

.article-section__site--locations-second {
  width: 130px;
  color: #ffffff;
  background-color: #c2e297;
  border-radius: 4px;
  text-align: center;
  margin: 43px 120px 25px 20px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.04em;
  line-height: 186%;
}

.article-section__site--paragraph {
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  color: #907e82;
}

.article-section__site--city,
.article-section__site--paragraph,
.article-section__site--days,
.article-section__site--times,
.article-section__link--tel,
.article-section__link--mail {
  margin-left: 20px;
}

.article-section__list--link {
  display: flex;
  flex-direction: column;
  align-items: baseline;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
  }
}

.article-section__list--locations,
.article-section__list--franchise {
  width: 161px;
  height: 44px;
  text-align: center;
  padding: 10px 0 10px 0;
  border-radius: 22px;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
}

.article-section__list--locations {
  margin-bottom: 10px;

  @media screen and (min-width: 768px) {
    margin-right: 15px;
  }
}
